import React, { Component } from "react";
import TruncateString from "react-truncate-string";
import logo from "./logo.svg";
import "./App.css";

const string1 =
  "It truncates long long long long long long long long long long long long long long long long long long long long long long long long long long long long long long long long long long strings in the middle!";

class App extends Component {
  render() {
    return (
      <div className="App">
        <div className="container containerA">
          <TruncateString text={string1} />
        </div>
        <div className="container containerB">
          <TruncateString text={string1} />
        </div>
        <div className="container containerC">
          <TruncateString text={string1} />
        </div>
        <div className="container containerD">
          <TruncateString text={string1} />
        </div>
        <div className="container containerE">
          <TruncateString text={string1} />
        </div>

        <div>
          <h1>
            <a href="https://codesandbox.io/s/xvv9r5ozo">codesandbox</a>
          </h1>
        </div>
      </div>
    );
  }
}

export default App;



